.homebanner{
    background-color: #1F1F1F;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;    
}
.biometric{
    width: 50%;
    display: flex;
    justify-content: center;
}
.biometric img{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    -webkit-transform: rotate(22deg);
            transform: rotate(22deg);
}
.execute{
    display: flex;
    position: relative;
    flex-direction: column; 
    align-items: center;
    height: 50vh;
    width: 100%;
}
.execute .pos_cont{
    position: relative;
    top: 30px;
}
.execute .left_anime{
    -webkit-animation: execute_left 0.7s infinite;
            animation: execute_left 0.7s infinite;
    position: absolute;
    left: 0px;
}
@-webkit-keyframes execute_left {
    0%   {margin-left: -30px;}
    25%  {margin-left: -35px;}
    50%  {margin-left: -45px;}
    75%  {margin-left: -35px;}
    100% {margin-left: -30px;}
}
@keyframes execute_left {
    0%   {margin-left: -30px;}
    25%  {margin-left: -35px;}
    50%  {margin-left: -45px;}
    75%  {margin-left: -35px;}
    100% {margin-left: -30px;}
}
.execute .right_anime{
    -webkit-animation: excute_right 0.7s infinite;
            animation: excute_right 0.7s infinite;
    position: absolute;
    right: 0px;
}
@-webkit-keyframes excute_right {
    0%   {margin-right: -30px;}
    25%  {margin-right: -35px;}
    50%  {margin-right: -45px;}
    75%  {margin-right: -35px;}
    100% {margin-right: -30px;}
}
@keyframes excute_right {
    0%   {margin-right: -30px;}
    25%  {margin-right: -35px;}
    50%  {margin-right: -45px;}
    75%  {margin-right: -35px;}
    100% {margin-right: -30px;}
}
.execute button{
    position: static;
    width: 179px;
    height: 58px;
    font-family: 'Creepster', cursive;
    letter-spacing: 1px;
    background-color: #6763E0;
    border-radius: 5%; 
    border: none;
    border-width: 5px;
    padding: 11px;
    font-size: x-large;
}
.bothContent{
    height: 100vh;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.bannerText p{
    font-size: 30px;
    color: #8FBC8F;
    font-family: 'Electrolize', sans-serif;
}
.bannerText h1{
    font-family: 'Electrolize', sans-serif;
    color: #90EE90;
    font-size: 100px;
    letter-spacing: 2px;
}
.code{
    position: absolute;
    top: 130px;
    width: 100%;
    text-align: center;
}
.code span#request{
    color: #6763E0;
    font-size: 7rem;
    font-family: 'Electrolize', sans-serif;
    font-weight: 900;
    text-transform: uppercase;
    -webkit-animation: vanish;
            animation: vanish;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}
@-webkit-keyframes vanish {
    0%{
        opacity: 1;
      }
      100%{
        display: none;
        opacity: 0;
      }
}
@keyframes vanish {
    0%{
        opacity: 1;
      }
      100%{
        display: none;
        opacity: 0;
      }
}
.code span.executing{
    font-family: 'Electrolize', sans-serif;
    color: #90EE90;
    position: absolute;
    top: 10px;
    left: 0;
    font-size: 1.5rem;
}
span.executing p{
    width: 0;
    white-space: nowrap;
    overflow: hidden;
}
