.Detailed_Content{  
    height: 85vh;
    width: 100%;
    background-color: #3d3d3d;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.DE_container{
    width: 70%;
    display: flex;
    flex-direction: column;
    font-family: 'Electrolize', sans-serif;
    color: #FFFFFE;
}
.resume_download{
    margin-top: 20px;
    align-items: center;
    justify-content: center;
    height: 15vh;
    width: 73%;
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: row;
}
.resume_download p{
    width: 50%;
    font-size: 1.2em;
    color: #FFFFFE;
    align-items: center;
    font-family: 'Electrolize', sans-serif;
    padding: 1rem;
}
.resume_download p:nth-child(1){
    background: linear-gradient(to right, #FE7772, #FF896B, #FFA662);
    border-radius: 0.5rem;
}
div.btn button{
    margin-top: 10px;
    padding: 5px;
    border-radius: 0.25rem;
    background-color: transparent;
    border-style: solid;
    border-color: #6763E0;
    color: #6763E0;
    height: 30px;
    width: 70px;
    font-size: 0.9rem;
    font-family: 'Electrolize', sans-serif;
    font-weight: 900;
}
div.btn button a{
    text-decoration: none;
    color: #000000;
}
h1.Headings{
    font-size: 3rem;
    color: #6763E0;
}
div.para_img{
    width: 100%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
}
div.para_img p,div.para_img img{
    width: 50%;
}
div.para_img img{
    border-radius: 2rem;
}
div.para_img p{
    line-height: 22px;
    word-spacing: 3px;
    letter-spacing: 1px;
    font-size: 1.2em;
    padding-right: 50px;
}
div.para_img p b{
    text-shadow: -3px 2px 2px #6763e0;
}

div.comments{
    width: 100%;
    height: 80vh;
    position: relative;
    background-color: #1F1F1F;
}
div.form{
    width: 50%;
    height: 40vh;
    position: relative;
    top: 45px;
    margin: auto;
}
div.form h1{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    color: #6763E0;
    font-family: 'Electrolize', sans-serif;
}
div.form form{
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
div.form form input{
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 30px;
    border-radius: 0.25rem;
    border: 0 solid;
    outline: none;
    color: #6763E0;
    font-weight: 900;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-family: 'Electrolize', sans-serif;
}
div.form form label{
    font-size: 1.3rem;
    font-weight: 700;
    color: #FFFFFE;
}
div.btn{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

div.btn button.active{
    background-color: #6763E0;
    color: #000;
}
div.btn button.deactive:hover{
    background-color: #6763E0;
    color: #000;
    transition-duration: 0.7s;
}
.contacts{
    width: 50%;
    height: 40vh;
    margin: auto;
}
div.form form textarea{
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 0.25rem;
    border: 0 solid;
    outline: none;
    font-weight: 900;
    font-size: 1.2rem;
    font-family: 'Electrolize', sans-serif;
}
div.footer{
    bottom: 0;
    height: 20vh;
    width: 100%;
    background-color: #1F1F1F;
    display: flex;
    flex-direction: column;
}
div.footer p{
    color: #6763E0;
    font-family: 'Electrolize', sans-serif;
    font-size: 2rem;
    font-weight: 900;
    margin: auto;
}
div.footer_con{
    width: 35%;
    height: 8vh;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
div.footer_con a img{
    height: 50px;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}
div.footer_con img:hover{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    transition-duration: 0.4s;
}
div.img_att{
    margin: auto;
}
div.img_att a{
    color: #6763E0;
    font-family: 'Electrolize', sans-serif;
    font-size: 1rem;
    text-decoration: none;
}
@media only screen and (max-width: 1400px) 
{   
    .biometric img{
        width: 355px;
        height: 355px;
    }
    .code span#request{
        font-size: 5.7rem;  
    }
    .bannerText{
        width: 95%;
    }
}
@media only screen and (max-width: 1200px) 
{
    .biometric img{
        width: 355px;
        height: 355px;
    }
    .code span#request{
        font-size: 4.7rem;  
    }
    .bannerText{
        width: 80%;
    }
}
@media only screen and (max-width: 992px) 
{
    .biometric img {
        object-fit: cover;
        width: 296px;
        height: 296px;
    }
    .biometric{
        margin-top: 80px;
    }
    .execute{
        display: none;
    }
    .homebanner{
        flex-direction: column-reverse;
    }
    .bothContent{
        width: 100%;
    }
    .bannerText h1,.bannerText p{
        text-align: center;
    }
    div.footer_con{
        width: 60%;
    }
    div.para_img img{
        display: none;
    }
    .Detailed_Content{
        background: url("/src/MyComponents/media/About.jpg"),rgba(0, 0, 0, 0.8);
        background-blend-mode: darken;
        background-size: contain;
        height: 100vh;
    }
    .DE_container{
        width: 80%;
    }
    div.para_img{
        flex-wrap: nowrap;
        justify-content: baseline;
        align-items: baseline;
    }
    div.para_img p{
        width: 100%;
        padding-right: 0;
    }
    div.footer_con a img{
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
    }
    .resume_download{
        height: 25vh;
        width: 80%;
        padding: 0;
    }
}
